.nav {
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-gray);
    transition: color 0.3s;

    &:hover {
      color: var(--color-accent);
      transition: color 0.3s;
    }

    &--current {
      color: var(--color-accent);
    }
  }
}

.basket {
  position: relative;
  display: flex;
  margin-top: 67px;
  margin-bottom: 150px;
  width: 30px;

  &__link {
    svg {
      width: 24px;
      height: 24px;
      fill: var(--color-zircon);
      transition: fill 0.3s;
    }
  }

  &__num {
    position: absolute;
    left: 20px;
    top: -10px;
    z-index: 1;
  }

  &:hover {
    svg {
      fill: var(--color-accent);
      transition: fill 0.3s;
    }

    .quantity {
      color: var(--color-accent);
      transition: background 0.3s, color 0.3s;
      &::before {
        background: var(--color-zircon);
      }
    }
  }

  @include sm {
    margin-bottom: 65px;
  }
}

.quantity {
  position: relative;
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: var(--color-zircon);
  transition: color 0.3s;

  &::before {
    content: "";
    position: absolute;
    left: -5px;
    top: 0;
    z-index: -1;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background: var(--color-accent);
    transition: background 0.3s;
  }
}