html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: var(--dark-gray);
  background-color: var(--color-concrete);
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

@media (max-width: 1500px) {
  .container {
    padding: 0 30px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.heading {
  margin-bottom: 70px;
  text-align: center;
  color: var(--color-primary);
}

.heading__subtitle {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}

.heading__title {
  font-family: "Montserrat";
  font-weight: 900;
  font-size: 30px;
  line-height: 34px;
  text-transform: uppercase;
}

.header {
  overflow: hidden;
  height: 100vh;
  background: #f9fbff;
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.25));
  user-select: none;
}

.header__container {
  position: relative;
  display: flex;
}

.one__box {
  display: flex;
}

.one__box-display {
  position: relative;
  margin-top: 70px;
  width: 320px;
}

@media (max-width: 1500px) {
  .one__box-display {
    width: 280px;
  }
}

@media (max-width: 992px) {
  .one__box-display {
    width: 190px;
  }
}

.one__box-title {
  position: absolute;
  left: 0;
  bottom: 10%;
  z-index: 1;
  width: 455px;
  font-family: "Montserrat";
  font-weight: 900;
  font-size: 54px;
  line-height: 70px;
  text-transform: uppercase;
  color: var(--color-primary);
}

.one__box-title span {
  color: var(--color-zircon);
  background: var(--color-accent);
}

@media (max-width: 1500px) {
  .one__box-title {
    bottom: 25%;
  }
}

@media (max-width: 1200px) {
  .one__box-title {
    font-size: 45px;
    line-height: 55px;
  }
}

@media (max-width: 576px) {
  .one__box-title {
    width: 250px;
    font-size: 30px;
    line-height: 38px;
  }
}

.one__box-video {
  position: relative;
  width: 700px;
  height: 725px;
  background: url("../img/video.jpg") center no-repeat;
  background-size: cover;
}

.video__play {
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background: var(--color-zircon);
  transform: translate(-50%, -50%);
  transition: background 0.3s;
}

.video__play svg {
  width: 10px;
  height: 10px;
  fill: var(--color-accent);
  transition: fill 0.3s;
}

.video__play:hover {
  background: var(--color-accent);
  transition: background 0.3s;
}

.video__play:hover svg {
  fill: var(--color-zircon);
  transition: fill 0.3s;
}

@media (max-width: 1500px) {
  .video__play {
    top: 43%;
  }
}

@media (max-width: 1200px) {
  .video__play {
    left: 35%;
  }
}

@media (max-width: 992px) {
  .video__play {
    left: 26%;
  }
}

.two__box {
  position: absolute;
  right: -15%;
  top: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  overflow: hidden;
  width: 350px;
  height: 100vh;
  max-height: 725px;
  background: var(--color-primary);
  transform: translateX(0);
  transition: transform 0.3s;
}

@media (max-width: 1500px) {
  .two__box-nav {
    margin-right: 120px;
  }
}

@media (max-width: 1500px) {
  .two__box {
    right: -12%;
  }
}

@media (max-width: 992px) {
  .two__box {
    right: -20%;
  }
}

@media (max-width: 768px) {
  .two__box {
    transform: translateX(82%);
    transition: transform 0.3s;
  }
}

.two__box--visible {
  transform: translateX(0);
  transition: transform 0.3s;
}

.hide {
  display: none;
}

@media (max-width: 1200px) {
  video {
    width: 65%;
    height: 100vh;
  }
}

* {
  margin: 0;
  padding: 0;
}

@media (max-width: 768px) {
  .pulse {
    position: absolute;
    right: 2%;
    bottom: 53%;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 5px;
    transform: translateY(-50%);
    animation: radial-pulse 1s infinite;
  }
  .pulse svg {
    width: 30px;
    height: 20px;
    fill: var(--color-primary);
  }
}

@keyframes radial-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(65, 81, 103, 0.5);
  }
  100% {
    box-shadow: 0 0 0 15px rgba(65, 81, 103, 0);
  }
}

.hide {
  display: none;
}

.nav__item:not(:last-child) {
  margin-bottom: 20px;
}

.nav__link {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: var(--light-gray);
  transition: color 0.3s;
}

.nav__link:hover {
  color: var(--color-accent);
  transition: color 0.3s;
}

.nav__link--current {
  color: var(--color-accent);
}

.basket {
  position: relative;
  display: flex;
  margin-top: 67px;
  margin-bottom: 150px;
  width: 30px;
}

.basket__link svg {
  width: 24px;
  height: 24px;
  fill: var(--color-zircon);
  transition: fill 0.3s;
}

.basket__num {
  position: absolute;
  left: 20px;
  top: -10px;
  z-index: 1;
}

.basket:hover svg {
  fill: var(--color-accent);
  transition: fill 0.3s;
}

.basket:hover .quantity {
  color: var(--color-accent);
  transition: background 0.3s, color 0.3s;
}

.basket:hover .quantity::before {
  background: var(--color-zircon);
}

@media (max-width: 768px) {
  .basket {
    margin-bottom: 65px;
  }
}

.quantity {
  position: relative;
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: var(--color-zircon);
  transition: color 0.3s;
}

.quantity::before {
  content: "";
  position: absolute;
  left: -5px;
  top: 0;
  z-index: -1;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: var(--color-accent);
  transition: background 0.3s;
}

.features {
  margin-bottom: 100px;
}

.features__container {
  padding-top: 80px;
}

.features__item {
  position: relative;
  display: inline-block;
  width: 24%;
  text-align: center;
}

.features__item:not(:last-child) {
  margin-right: 11px;
}

@media (max-width: 1200px) {
  .features__item {
    margin-bottom: 20px;
    width: 48%;
  }
}

@media (max-width: 768px) {
  .features__item {
    width: 100%;
  }
}

.features__img {
  margin-bottom: 25px;
}

.features__title {
  margin-bottom: 12px;
}

.features__title-link {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: var(--ebony-clay);
}

.features__title-link::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.features__descr {
  padding: 0 34px;
  line-height: 22px;
}

.coffee {
  position: relative;
  height: 900px;
  background: var(--color-zircon);
}

.coffee::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 385px;
  height: 100%;
  background: var(--color-primary);
}

@media (max-width: 1200px) {
  .coffee::before {
    width: 270px;
  }
}

@media (max-width: 992px) {
  .coffee::before {
    width: 180px;
  }
}

@media (max-width: 768px) {
  .coffee::before {
    width: 120px;
  }
}

@media (max-width: 576px) {
  .coffee::before {
    display: none;
  }
}

.coffee__container {
  padding-top: 80px;
}

.coffee__box {
  position: absolute;
  left: 58%;
  top: 56%;
  z-index: 1;
  transform: translate(-50%, -50%);
}

@media (max-width: 1500px) {
  .coffee__box {
    margin-left: 30px;
  }
}

.coffee__item {
  position: relative;
  display: flex;
  margin-bottom: 24px;
  width: 520px;
  background: var(--color-white);
}

.coffee__item:hover {
  filter: drop-shadow(0 4px 24px rgba(205, 205, 205, 0.5));
  transition: 0.3s;
}

.coffee__item:nth-child(2n) {
  margin-bottom: 0;
}

@media (max-width: 1500px) {
  .coffee__item {
    width: 460px;
    height: 250px;
  }
}

@media (max-width: 1200px) {
  .coffee__item {
    width: 415px;
    height: 230px;
  }
}

.coffee__content {
  padding: 40px 10px;
  width: 260px;
}

.coffee__price {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: var(--color-accent);
}

.coffee__info-title {
  margin-bottom: 24px;
}

.coffee__info-title--link {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: var(--ebony-clay);
}

.coffee__info-descr {
  margin-bottom: 35px;
  line-height: 22px;
}

@media (max-width: 1200px) {
  .coffee__info-descr {
    margin-bottom: 14px;
  }
}

.coffee__box {
  overflow: hidden;
  padding-right: 10%;
  width: 100%;
  max-width: 1300px;
}

.coffee__box .swiper-wrapper {
  display: flex;
}

.coffee__box .swiper-slide {
  height: 100%;
}

@media (max-width: 1200px) {
  .coffee__box {
    overflow-x: hidden;
    margin-left: 0;
    padding-right: 0;
  }
}

.coffee-prev-btn,
.coffee-next-btn {
  position: absolute;
  left: 10%;
  top: 56%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background: var(--color-accent);
  transform: translateY(-50%);
  transition: background 0.3s;
}

.coffee-prev-btn svg,
.coffee-next-btn svg {
  width: 40px;
  height: 20px;
  fill: var(--color-zircon);
  transition: fill 0.3s;
}

.coffee-prev-btn:hover,
.coffee-next-btn:hover {
  background: var(--color-zircon);
  transition: background-color 0.3s;
}

.coffee-prev-btn:hover svg,
.coffee-next-btn:hover svg {
  fill: var(--color-accent);
  transition: fill 0.3s;
}

@media (max-width: 1500px) {
  .coffee-prev-btn,
  .coffee-next-btn {
    left: 5%;
  }
}

@media (max-width: 1200px) {
  .coffee-prev-btn,
  .coffee-next-btn {
    left: 1%;
  }
}

@media (max-width: 992px) {
  .coffee-prev-btn,
  .coffee-next-btn {
    left: 40%;
    top: 90%;
  }
}

.coffee-next-btn {
  left: auto;
  right: 7%;
}

@media (max-width: 1500px) {
  .coffee-next-btn {
    right: 4%;
  }
}

@media (max-width: 1500px) {
  .coffee-next-btn {
    right: 1%;
  }
}

@media (max-width: 992px) {
  .coffee-next-btn {
    right: 30%;
  }
}

.giftset {
  position: relative;
  height: 100vh;
  max-height: 800px;
  background: var(--light-gray);
}

.giftset::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 320px;
  height: 100vh;
  background: var(--color-zircon);
}

@media (max-width: 992px) {
  .giftset::before {
    width: 200px;
  }
}

@media (max-width: 768px) {
  .giftset::before {
    width: 130px;
  }
}

@media (max-width: 576px) {
  .giftset::before {
    display: none;
  }
}

.giftset__container {
  padding-top: 80px;
}

@media (max-width: 576px) {
  .giftset {
    display: none;
  }
}

.tabs {
  position: absolute;
  left: 55%;
  top: 60%;
  display: flex;
  height: 450px;
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  .tabs {
    left: 50%;
    height: 378px;
  }
}

.tabs-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
}

@media (max-width: 992px) {
  .tabs-nav__list {
    height: 415px;
  }
}

@media (max-width: 768px) {
  .tabs-nav__list {
    height: 370px;
  }
}

.tabs-nav__item {
  padding: 57px 30px;
  font-family: "Montserrat";
  font-weight: 900;
