  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background: var(--color-accent);
  transform: translateY(-50%);
  transition: background 0.3s;
}

.combo-prev-btn svg,
.combo-next-btn svg {
  width: 40px;
  height: 20px;
  fill: var(--color-zircon);
  transition: fill 0.3s;
}

.combo-prev-btn:hover,
.combo-next-btn:hover {
  background: var(--color-zircon);
  transition: background-color 0.3s;
}

