.header {
  overflow: hidden;
  height: 100vh;
  background: #f9fbff;
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.25));
  user-select: none;

  &__container {
    position: relative;
    display: flex;
  }
}

.one__box {
  display: flex;

  &-display {
    position: relative;
    margin-top: 70px;
    width: 320px;

    @include lg {
      width: 280px;
    }

    @include md {
      width: 190px;
    }
  }

  &-title {
    position: absolute;
    left: 0;
    bottom: 10%;
    z-index: 1;
    width: 455px;
    font-family: "Montserrat";
    font-weight: 900;
    font-size: 54px;
    line-height: 70px;
    text-transform: uppercase;
    color: var(--color-primary);

    span {
      color: var(--color-zircon);
      background: var(--color-accent);
    }

    @include lg {
      bottom: 25%;
    }

    @include md-max {
      font-size: 45px;
      line-height: 55px;
    }

    @include xs {
      width: 250px;
      font-size: 30px;
      line-height: 38px;
    }
  }

  &-video {
    position: relative;
    width: 700px;
    height: 725px;
    background: url("../img/video.jpg") center no-repeat;
    background-size: cover;
  }
}

.video {
  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    background: var(--color-zircon);
    transform: translate(-50%, -50%);
    transition: background 0.3s;

    svg {
      width: 10px;
      height: 10px;
      fill: var(--color-accent);
      transition: fill 0.3s;
    }

    &:hover {
      background: var(--color-accent);
      transition: background 0.3s;

      svg {
        fill: var(--color-zircon);
        transition: fill 0.3s;
      }
    }

    @include lg {
      top: 43%;
    }

    @include md-max {
      left: 35%;
    }

    @include md {
      left: 26%;
    }
  }
}

.two__box {
  position: absolute;
  right: -15%;
  top: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  overflow: hidden;
  width: 350px;
  height: 100vh;
  max-height: 725px;
  background: var(--color-primary);
  transform: translateX(0);
  transition: transform 0.3s;

  &-nav {
    @include lg {
      margin-right: 120px;
    }
  }

  @include lg {
    right: -12%;
  }

  @include md {
    right: -20%;
  }

  @include sm {
    transform: translateX(82%);
    transition: transform 0.3s;
  }

  &--visible {
    transform: translateX(0);
    transition: transform 0.3s;
  }
}

.hide {
  display: none;
}

video {
  @include md-max {
    width: 65%;
    height: 100vh;
  }
}

* {
  margin: 0;
  padding: 0;
}

.pulse {
  @include sm {
    position: absolute;
    right: 2%;
    bottom: 53%;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 5px;
    transform: translateY(-50%);
    animation: radial-pulse 1s infinite;

    svg {
      width: 30px;
      height: 20px;
      fill: var(--color-primary);
    }
  }
}

@keyframes radial-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(65, 81, 103, 0.5);
  }

  100% {
    box-shadow: 0 0 0 15px rgba(65, 81, 103, 0);
  }
}

.hide {
  display: none;
}
