.features {
  margin-bottom: 100px;

  &__container {
    padding-top: 80px;
  }

  &__item {
    position: relative;
    display: inline-block;
    width: 24%;
    text-align: center;

    &:not(:last-child) {
      margin-right: 11px;
    }

    @include md-max {
      margin-bottom: 20px;
      width: 48%;
    }

    @include sm {
      width: 100%;
    }
  }

  &__img {
    margin-bottom: 25px;
  }

  &__title {
    margin-bottom: 12px;

    &-link {
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;
      color: var(--ebony-clay);

      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
      }
    }
  }

  &__descr {
    padding: 0 34px;
    line-height: 22px;
  }
}
