.giftset {
  position: relative;
  height: 100vh;
  max-height: 800px;
  background: var(--light-gray);

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 320px;
    height: 100vh;
    background: var(--color-zircon);

    @include md {
      width: 200px;
    }

    @include sm {
      width: 130px;
    }

    @include xs {
      display: none;
    }
  }

  &__container {
    padding-top: 80px;
  }

  @include xs {
    display: none;
  }
}

.tabs {
  position: absolute;
  left: 55%;
  top: 60%;
  display: flex;
  height: 450px;
  transform: translate(-50%, -50%);

  @include sm {
    left: 50%;
    height: 378px;
  }
}

.tabs-nav {
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;

    @include md {
      height: 415px;
    }

    @include sm {
      height: 370px;
    }
  }

  &__item {
    padding: 57px 30px;
    font-family: "Montserrat";
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
    color: var(--color-primary);
    background: var(--light-gray);
    opacity: 0.3;
    cursor: pointer;

    @include md {
      padding: 50px 30px;
    }

    @include sm {
      padding: 46px 25px;
    }
  }
}

.is-active {
  box-shadow: 5px 7px 10px 0 rgba(0, 0, 0, 0.2);
  background: var(--color-white);
  opacity: 1;
  animation: scale 1s ease-in-out;
}

.tab {
  position: relative;
  display: none;
  width: 875px;
  box-shadow: -11px 13px 10px 0 rgba(0, 0, 0, 0.2);
  background: var(--color-white);

  &--active {
    display: block;
    animation: scale 1s ease-in-out;
  }

  &__img {
    position: absolute;
    left: -50%;
    top: 57%;
    transform: translate(50%, -50%);

    @include md-max {
      width: 60%;
    }

    @include sm {
      display: none;
    }
  }

  &__box {
    padding: 54px 50px 54px 375px;

    @include md-max {
      padding: 54px 50px 54px 280px;
    }

    @include md {
      padding: 20px 50px 54px 205px;
    }

    @include sm {
      padding: 25px 50px;
    }
  }

  &__content {
    width: 430px;

    &-price {
      margin-bottom: 12px;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: var(--color-accent);
    }

    &-title {
      margin-bottom: 12px;
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;
      color: var(--ebony-clay);

      @include md {
        font-size: 18px;
      }
    }

    &-descr {
      margin-bottom: 24px;
      font-size: 16px;
      line-height: 22px;

      @include md {
        font-size: 14px;
      }
    }

    @include md {
      width: 260px;
    }

    @include sm {
      width: 350px;
    }
  }

  &__icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 46px;

    &-one {
      display: flex;
      align-items: center;
    }

    &-two {
      display: flex;
      align-items: center;
      margin-right: 40px;
    }

    @include md {
      margin-bottom: 15px;
    }
  }

  &__icon {
    margin-right: 24px;

    svg {
      width: 30px;
      height: 30px;

      @include md {
        width: 20px;
        height: 20px;
      }
    }

    @include md {
      margin-right: 12px;
    }
  }

  &__info {
    line-height: 22px;
    color: var(--color-primary);

    &-title {
      @include md {
        font-size: 13px;
      }
    }

    &-subtitle {
      font-weight: 300;
    }
  }

  &__buttons {
    display: flex;
    justify-content: space-between;

    &:last-child {
      margin-right: 40px;
    }

    &-btn {
      border-radius: 20px;
      padding: 8px 36px;
      font-weight: 700;
      line-height: 22px;
      color: var(--color-primary);
      background: var(--color-white);
      transition: background 0.2s, color 0.2s;

      &--current {
        color: var(--color-zircon);
        background: var(--color-accent);
      }

      &:hover {
        color: var(--color-zircon);
        background: var(--color-accent);
        transition: all 0.2s;
      }

      @include md {
        padding: 8px;
      }

      @include xs {
        margin-bottom: 10px;
        width: 100%;
      }
    }
  }

  @include md-max {
    width: 740px;
  }

  @include md {
    width: 530px;
    height: 415px;
  }

  @include sm {
    width: 430px;
    height: 377px;
  }
}

@keyframes scale {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }

  50% {
    opacity: 0.5;
    transform: scale(1.005);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
