.combo {
  position: relative;
  height: 900px;
  background: var(--color-zircon);

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 320px;
    height: 100%;
    background: var(--color-primary);

    @include md {
      width: 200px;
    }

    @include sm {
      width: 130px;
    }

    @include xs {
      display: none;
    }
  }

  &__container {
    padding-top: 80px;
    padding-bottom: 100px;
  }

  &__box {
    position: absolute;
    left: 56%;
    top: 56%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }

  &__item {
    position: relative;
    width: 33.33333%;

    &:hover {
      filter: drop-shadow(0 4px 24px rgba(205, 205, 205, 0.5));
      transition: 0.3s;
    }

    @include xs {
      width: 100%;
    }
  }

  &__content {
    padding: 50px;
    width: 375px;
    background: var(--color-white);

    @include lg {
      width: 320px;
    }

    @include md-max {
      padding: 35px;
      width: 285px;
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 42px;
    width: 375px;
    height: 300px;
    background: var(--light-gray);

    @include lg {
      width: 320px;
    }

    @include md-max {
      width: 285px;
    }
  }

  &__price {
    margin-bottom: 10px;

    &-new {
      margin-right: 10px;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: var(--color-accent);
    }

    &-old {
      line-height: 19px;
      text-decoration-line: line-through;
      color: var(--color-primary);
    }
  }

  &__info {
    margin-bottom: 32px;

    &-title {
      &--link {
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: var(--ebony-clay);

        &::before {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }

        @include lg {
          font-size: 20px;
        }

        @include md-max {
          font-size: 17px;
        }
      }
    }

    &-descr {
      line-height: 22px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-btn {
      border-radius: 20px;
      padding: 6px 17px;
      font-weight: 700;
      line-height: 22px;
      text-transform: uppercase;
      color: var(--color-primary);
      background: var(--color-white);
      transition: background 0.2s, color 0.2s;

      &:hover {
        color: var(--color-zircon);
        background: var(--color-accent);
        transition: all 0.2s;
      }

      &--current {
        color: var(--color-zircon);
        background: var(--color-accent);
      }

      @include lg {
        padding: 6px 10px;
      }

      @include md-max {
        padding: 6px;
      }
    }
  }
}

.combo__box {
  overflow: hidden;
  padding-right: 10%;
  width: 100%;
  max-width: 1345px;

  .swiper-wrapper {
    display: flex;
  }

  .swiper-slide {
    height: 100%;
  }

  @include md-max {
    padding-right: 0;
  }

  @include xs {
    max-width: 350px;
  }
}

.combo-prev-btn,
.combo-next-btn {
  position: absolute;
  left: 7%;
  top: 55%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background: var(--color-accent);
  transform: translateY(-50%);
  transition: background 0.3s;

  svg {
    width: 40px;
    height: 20px;
    fill: var(--color-zircon);
    transition: fill 0.3s;
  }

  &:hover {
    background: var(--color-zircon);
    transition: background-color 0.3s;

    svg {
      fill: var(--color-accent);
      transition: fill 0.3s;
    }
  }

  @include md {
    left: 40%;
    top: 93%;
  }
}

.combo-next-btn {
  left: auto;
  right: 7%;

  @include md {
    right: 30%;
  }
}

.swiper-slide {
  opacity: 0.7;
  transition: 0.5s;
}

.swiper-slide-active,
.swiper-slide-prev,
.swiper-slide-next {
  opacity: 1;
  transition: 0.5s;
}
