html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: var(--dark-gray);
  background-color: var(--color-concrete);
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;

  @include lg {
    padding: 0 30px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.heading {
  margin-bottom: 70px;
  text-align: center;
  color: var(--color-primary);

  &__subtitle {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }

  &__title {
    font-family: "Montserrat";
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
    text-transform: uppercase;
  }
}
