.coffee {
  position: relative;
  height: 900px;
  background: var(--color-zircon);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 385px;
    height: 100%;
    background: var(--color-primary);

    @include md-max {
      width: 270px;
    }

    @include md {
      width: 180px;
    }

    @include sm {
      width: 120px;
    }

    @include xs {
      display: none;
    }
  }

  &__container {
    padding-top: 80px;
  }

  &__box {
    position: absolute;
    left: 58%;
    top: 56%;
    z-index: 1;
    transform: translate(-50%, -50%);

    @include lg {
      margin-left: 30px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    margin-bottom: 24px;
    width: 520px;
    background: var(--color-white);

    &:hover {
      filter: drop-shadow(0 4px 24px rgba(205, 205, 205, 0.5));
      transition: 0.3s;
    }

    &:nth-child(2n) {
      margin-bottom: 0;
    }

    @include lg {
      width: 460px;
      height: 250px;
    }

    @include md-max {
      width: 415px;
      height: 230px;
    }
  }

  &__content {
    padding: 40px 10px;
    width: 260px;
  }

  &__price {
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: var(--color-accent);
  }

  &__info {
    &-title {
      margin-bottom: 24px;

      &--link {
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: var(--ebony-clay);
      }
    }

    &-descr {
      margin-bottom: 35px;
      line-height: 22px;

      @include md-max {
        margin-bottom: 14px;
      }
    }
  }
}

.coffee__box {
  overflow: hidden;
  padding-right: 10%;
  width: 100%;
  max-width: 1300px;

  .swiper-wrapper {
    display: flex;
  }

  .swiper-slide {
    height: 100%;
  }

  @include md-max {
    overflow-x: hidden;
    margin-left: 0;
    padding-right: 0;
  }
}

.coffee-prev-btn,
.coffee-next-btn {
  position: absolute;
  left: 10%;
  top: 56%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background: var(--color-accent);
  transform: translateY(-50%);
  transition: background 0.3s;

  svg {
    width: 40px;
    height: 20px;
    fill: var(--color-zircon);
    transition: fill 0.3s;
  }

  &:hover {
    background: var(--color-zircon);
    transition: background-color 0.3s;

    svg {
      fill: var(--color-accent);
      transition: fill 0.3s;
    }
  }

  @include lg {
    left: 5%;
  }

  @include md-max {
    left: 1%;
  }

  @include md {
    left: 40%;
    top: 90%;
  }
}

.coffee-next-btn {
  left: auto;
  right: 7%;

  @include lg {
    right: 4%;
  }

  @include lg {
    right: 1%;
  }

  @include md {
    right: 30%;
  }
}
